    display:flex;
    justify-content: space-between;
    padding:0px 30px;
    position:sticky;
    bottom:0px;
    
    
}
.cv footer div{
    color:#706e6e;
    /* border:1px solid red; */
    padding:10px;
}
.cv footer div i{
    padding: 0px 10px;
    font-size: 20px;
}
* {
  margin: 0;
  box-sizing: border-box;
