.aventura{
  background-color: black;
  color: white;
}
.layout {
  
  height: 100vh;
  max-width: 500px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: "Hina Mincho", serif;
}

.historia {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
  color: white;
}

.opciones {
  color: white;
}

.opcion {
  display: flex;
  grid-gap: 15px;
  gap: 15px;
  margin-bottom: 10px;
}

.recordatorio {
  color: white;
  margin-top: 60px;
}

.botones {
  box-shadow: inset 0px 1px 0px 0px #ffffff;
  background: linear-gradient(to bottom, #f9f9f9 5%, #e9e9e9 100%);
  background-color: #f9f9f9;
  border-radius: 6px;
  border: 1px solid #dcdcdc;
  display: inline-block;
  cursor: pointer;
  color: #000000;
  font-family: Arial;
  font-size: 15px;
  font-weight: bold;
  padding: 6px 24px;
  text-decoration: none;
  text-shadow: 0px 1px 0px #ffffff;
}
.botones:hover {
  background: linear-gradient(to bottom, #e9e9e9 5%, #f9f9f9 100%);
  background-color: #e9e9e9;
}
.botones:active {
  position: relative;
  top: 1px;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0
}

body {
    font-family: 'Poppins';
    background-color: #FAEEC7
}

.app {
    height: 100vh;
    display: flex;
    flex-direction: column;
}

header {
    padding: 10px 15px;
    text-align: center;
}

header h1 {
    font-size: 24px
}

main {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-grow: 1;
    padding-bottom: 15px
}

main .listado-perritos {
    display: flex;
    flex-wrap: wrap;
    flex-grow: 1;
    align-content: flex-start;
    grid-gap: 10px;
    gap: 10px;
    padding: 15px;
}

main .listado-perritos .perrito {
    width: 32%;
    display: flex;
    grid-gap: 15px;
    gap: 15px;
    align-items: center;
    border-radius: 5px;
    background-color: #fff;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

main .listado-perritos .perrito img {
    border-radius: 5px 0 0 5px;
    width: 30%;
}

main .opciones-tamanio {
    display: flex;
    justify-content: center;
    grid-gap: 10px;
    gap: 10px;
    text-align: center;
    padding: 15px
}

main .opciones-tamanio button.opcion {
    padding: 25px 15px;
    background-color: #fff;
    border: none;
    border-radius: 5px;
    font-size: 12px;
    text-align: center;
    letter-spacing: 2px;
    text-transform: uppercase;
    cursor: pointer;
}
main .opciones-tamanio button.opcion:hover{
    /* box-shadow: 5px 5px  rgb(173, 172, 172) inset; */
    box-shadow: 1px 1px 1px rgb(221, 221, 221) inset;

}
